<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="原因编码" prop="reasonCode">
              <a-input
                v-model="entity.reasonCode"
                placeholder="请输入原因编码"
                :disabled="!!id"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="原因名称" prop="reasonName">
              <a-input
                v-model="entity.reasonName"
                placeholder="请输入原因名称"
                @change="inChange"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="原因类别" prop="category">
              <a-select
                v-model="entity.category"
                placeholder="请选择原因类别"
              >
                <a-select-option value="操作确认">操作确认</a-select-option>
                <a-select-option value="审核确认">审核确认</a-select-option>
                <a-select-option value="质量确认">质量确认</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="状态" prop="status">
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
              >
                <a-select-option value="启用">启用</a-select-option>
                <a-select-option value="禁用">禁用</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
        </a-row>

        <a-row :gutter="24">
          <a-col :sm="24">
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述"
                :rows="4"
                :max-length="1000"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <g-collapse headerName="其他信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="创建人">
                <span>{{ entity.creator || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="创建时间">
                <span>{{ entity.createTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="最后修改人">
                <span>{{ entity.modifier || 'admin' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="最后修改时间">
                <span>{{ entity.modifyTime || '2024-01-01 12:00:00' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditESigReason",
  mixins: [GNdoPageEditBase],

  data() {
    return {
      entity: {
        reasonCode: "",
        reasonName: "",
        category: undefined,
        status: "启用",
        description: "",
        creator: "",
        createTime: "",
        modifier: "",
        modifyTime: "",
      },
      rules: {
        reasonCode: [
          { required: true, message: "原因编码不能为空", trigger: "change" },
          {
            pattern: /^[A-Z0-9]+$/,
            message: "原因编码只能包含大写字母和数字",
            trigger: "change",
          },
        ],
        reasonName: [
          { required: true, message: "原因名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "名称不能包含前后空格",
            trigger: "change",
          },
        ],
        category: [
          { required: true, message: "请选择原因类别", trigger: "change" },
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" },
        ],
      },
    };
  },

  methods: {
    init() {
      this.loading = true;
      if (this.id) {
        // 模拟请求
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            reasonCode: "ESR1001",
            reasonName: "测试电子签名原因",
            category: "操作确认",
            status: "启用",
            description: "这是一个测试电子签名原因",
            creator: "admin",
            createTime: "2024-01-01 12:00:00",
            modifier: "admin",
            modifyTime: "2024-01-01 12:00:00",
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        reasonCode: "",
        reasonName: "",
        category: undefined,
        status: "启用",
        description: "",
        creator: "",
        createTime: "",
        modifier: "",
        modifyTime: "",
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.reasonName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },

    inChange(e) {
      console.log(e.target.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  background: #fff;
  padding: 24px;
  margin-bottom: 24px;
}
</style>